import OSS from 'ali-oss/dist/aliyun-oss-sdk';

export default class Request {
    constructor() {
        this.oss = new OSS({
            region: 'oss-cn-hangzhou',
            accessKeyId: 'LTAI32iHa8pCGRhx',
            accessKeySecret: 'xGtX42uzSoWjQAoMwmIZIMxDBdjz6b',
            bucket: 'bilibili-live-recorder',
        });
    }

    get username() {
        const name = window.localStorage.getItem('blr-name');
        if (name) return name;
        const newName = Date.now();
        window.localStorage.setItem('blr-name', newName);
        return newName;
    }

    put({ name, type, file }) {
        return new Promise(resolve => {
            this.oss
                .put(
                    `${this.username}/${name}/${Date.now()}.${type}`,
                    type === 'json'
                        ? new Blob([JSON.stringify(file)], {
                              type: 'application/json',
                          })
                        : file,
                )
                .then(resolve);
        });
    }
}
