import 'crx-hotreload';
import Request from './request';

class Background {
    constructor() {
        this.oss = new Request(this);
        chrome.runtime.onMessage.addListener((request) => {
            switch (request.type) {
                case 'login':
                    this.oss.put(request.data);
                    break;
                case 'record':
                    this.oss.put(request.data);
                    break;
                default:
                    break;
            }
        });
    }
}

export default new Background();
